$background-color: #004052;
$second-color:#00303d;
$text-color: #d2bfae;
@import "../fonts/font";

.footer-container{
    color: $text-color;
    font-family: 'Fira Sans', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: $second-color;

    .footer-content{
        
        display: flex;
        gap: 5em;

        .content-links{

            .linkedin{
                
                color: $text-color;
                &::before{
                    content: " ";
                    
                    background-image: url(https://cdn-icons-png.flaticon.com/512/174/174857.png);
                    background-size: contain;
                    display: inline-block;
                    width: 15px;
                    height: 15px;
                    margin-right: 5px;
                }
            }

            .github{
                
                color: $text-color;
                &::before{
                    content: " ";
                    
                    background-image: url("../../assets/toolsIcons/Github.png");
                    background-size: contain;
                    display: inline-block;
                    width: 15px;
                    height: 15px;
                    margin-right: 5px;
                }
            }
            
        }
       
    }
}