$background-color: #004052;
$second-color:#00303d;
$text-color: #d2bfae;
@import "../fonts/font";

.container-about-me{
   

    font-family: 'Fira Sans', sans-serif;
    background-color: $background-color;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    

    .about-me-content{
        margin: 1em;
        color: $text-color;
    }

    .about-me-icons{
        margin: 1em;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        
     
        background: rgba(0, 32, 41, 0.49);
        border-radius: 2em;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
       
        
        color: $text-color;
    
        .icons-container{
        
        display: flex;
         animation: slider 20s infinite ;
    
        @keyframes slider {
        0% {transform: translateX(0);}
        16% {transform: translateX(-54%);}
        32% {transform: translateX(-105%);}
        48% {transform: translateX(-157%);}
        64% {transform: translateX(-210%);}
        80% {transform: translateX(-257%);}
        
        }
    
        .icon-img{
            display: block;
            margin: .2em;
            width: 50%;
            border-radius: 50%;
            background-color: #f5f5f5;
        }
    
        }
    }
}

@media screen and (min-width: 768px) {
    .container-about-me{
        .about-me-icons{
            .icons-container{

            animation: karusel 10s infinite alternate-reverse ;
        
            @keyframes karusel {
                0% {transform: translateX(0);}
                100% {transform: translateX(-50%);}  
            }
            .icon-img{ 
                width: 20%;  
            }
            }
        }
    }
}

@media screen and (min-width: 1024px){
    .container-about-me{
        position: relative;
       flex-direction: row;
       &::before{
        opacity: .1;
        font-size: 15em;
        content: "About me";
        top: .3em;
       left: .4em;
        position: absolute;
        color: $text-color;
        
    }
    
    
        .about-me-content{
            z-index: 2;
            font-size: 1.5em;
            margin: 1em;
            color: $text-color;
           
            
        }
    
        .about-me-icons{
            background-color: transparent;
            box-shadow: none;
          
            font-size: 2em;
            position: relative;
            border-radius: 0;
            flex-basis: 80%;
        
            height: 50%;
            overflow: visible;
            
            .icons-container{
              
                align-items: end;
                flex-wrap: wrap;
                animation: none;
                overflow: visible;
                
               
                
               
                
            
                .icon-img{
                    background-color: #00303d;
                    
                    padding: 1em;
                    width: 10%;
                 
                    
                    
                    
                    
                    &:hover{
                        transform: scale(1.1);
                    }
                }
            
                }
            }
    }
}
   
    





        
        
            