$background-color: #004052;
$second-color:#00303d;
$text-color: #d2bfae;
@import "../fonts/font";

.container-nav{
    font-size: 1em;
    color: $text-color;
    display: flex;
    justify-content: space-around;
    font-family: 'Fira Sans', sans-serif;
    z-index: 11111111;
    position: fixed;
    background-color: $second-color;
    
    top: 0;
    width: 100vw;

    .nav-title{
        font-size: 1em;
        text-decoration: none;
        color: $text-color;
    }

    .nav-list{
        display: flex;
       
        justify-content: space-between;
        gap: 1em;
        list-style: none;

        .nav-item{
          
            text-decoration: none;
            color: $text-color;
            border: 1px solid $text-color;
            padding: .5em 2em;
            border-radius: 10px;
        }
    }
    
}

@media screen and (min-width: 1024px){
    .container-nav{
        font-size: 1.5em;
        
    
        .nav-list{
            
            .nav-item{
                transition: background-color 500ms ease-in-out;
                &:hover{
                        color: $background-color;
                        background-color: $text-color;
                   
                }
            }
        }
        
    }
}