$background-color: #004052;
$second-color:#00303d;
$text-color: #d2bfae;
@import "../fonts/font";

.wrapper{
    font-family: 'Fira Sans', sans-serif;
    position: relative;
    background-color: $background-color;
}

.main-container{
   
    background: linear-gradient(180deg, rgba(0,48,61,1) 70%, rgba(0,64,82,1) 100%);
    color: $text-color;
    height: 100vh;
    display: flex;
    position: relative;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   
    .main-title{
        font-size: 3rem;
        z-index: 2;

    }

    .main-para{
        
        z-index: 2;
        font-size: 1.3rem;

        .para-fancy{
          border-radius: 3px;
            background: linear-gradient(to right, $text-color 0%, rgb(0, 117, 150) 80%,$text-color 100%,);
            background-size: 200% ;

            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;

            animation: background-slide 3s linear infinite;

            @keyframes background-slide {
                from {
                    background-position: 0% center;
                    transform: scale(0);
                    
                }

                to {
                    background-position: -200% center;
                    transform: scale(1.1);
                }
            }
        }
        
    }

    .main-img{
    top: 10em;
    z-index: 1;
    position: absolute;
    opacity: .5;
    
    .img-first{
        width: 100%;
    }
   
    
    &::after{
        
        content: "";
        bottom: 0;
        left: 0;
        
        position: absolute;
    
        background: radial-gradient(circle, rgba(0, 64, 82, 0.301) 0%, rgba(0,48,61,1) 50%);
        width: 100%;
        height: 100%;
    }
    
    }
   
    .trailer{
        display: none;
    }
 
    .main-btn{
        border-radius: 10px;
        color: $text-color;
        border: 1px solid $text-color;
        padding: 1em 2em;
        background-color: $second-color;
        text-decoration: none;

       
    }
   
}

.tool-icons{
    margin: 1em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    
 
    background: rgba(0, 32, 41, 0.49);
    border-radius: 2em;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
   
    
    color: $text-color;

.icons-container{
    
display: flex;
animation: slider 20s infinite ;

@keyframes slider {
    0% {transform: translateX(0);}
    16% {transform: translateX(-54%);}
    32% {transform: translateX(-105%);}
    48% {transform: translateX(-157%);}
    64% {transform: translateX(-210%);}
    80% {transform: translateX(-257%);}
    
}

    .icon-img{
        display: block;
        margin: .2em;
        width: 50%;
        border-radius: 50%;
        background-color: #f5f5f5;
    }

    }
}

.about-me-article{
    
    margin: 1em;
    margin-top: 6em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: $background-color;
    color: $text-color;
   

    .article-content{

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        .article-btn{
            border-radius:10px ;
            padding: 1em;
            background-color: $background-color;
            text-decoration: none;
            border: 1px solid $text-color;
            text-decoration: none;
                color: $text-color;
            
    
            
        }
        
     }


    
}

.portfolio{

    .portfolio-title{
        font-size: 3em;
       text-align: center;
       color: $text-color;
    }

    .portfolio-para{
        
        
        text-align: center;
        color: $text-color;
    }

    .portfolio-container{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
       
        
        padding: 1em 0em 1em 0em;
        background: rgba(0, 32, 41, 0.49);
        border-radius: 10px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        .project{
            border-radius: 10px;
        
            flex: 0 0 50%;
           overflow: hidden;
          
           
            .project-img{
                display: block;
                width: 100%;
                height: 15em;
               
                
            }
            .project-desc{
                display: none;
                color: $text-color;
                margin: 1em;
                

                .desc-btn{
                    border-radius:10px ;
                    padding: 1em;
                    background-color: $background-color;
                    text-decoration: none;
                    border: 1px solid $text-color;
                    
                    
                    a{
                        text-decoration: none;
                        color: $text-color;
                    }
                }
            }

            &:hover > .project-desc{
                display: block;
                
            }

          
        }
    }
}

@media screen and (min-width: 768px) {

    .tool-icons{

        
    
    .icons-container{

        animation: karusel 10s infinite alternate-reverse ;
        
        @keyframes karusel {
            0% {transform: translateX(0);}
            100% {transform: translateX(-50%);}
            
            
        }
    
    
        .icon-img{
            
            width: 20%;
            
            
        }
    
        }
    }

    .about-me-article{
    
       font-size: 1.5em;
    
     
    
    
        
    
        
     
    }
    
    
}

@media screen and (min-width: 1024px) {

    .main-container{
        position: relative;
        align-items: center;
        justify-content: center;
        

        
        
        .main-title{
            
            
            font-size: 5rem;
            z-index: 2;

          
    
        }
    
        .main-para{
            
            z-index: 2;
            font-size: 2.5rem;
           
           
    
       
           
            

            
        }
    
        .main-img{
            
        z-index: 1;
        right: 5em;
        bottom: 18.5em;
        position: absolute;
        opacity: .5;
        
       
        
        
        
        }
        
        .trailer{
           display: block;
       
            

            position:fixed;

            left: 0;
            top: 0;
            z-index: 100000;

            pointer-events: none;
            opacity: 0;
            transition: opacity 500ms ease;

          

        }
       
       
    
        &:hover > .trailer{
          
            opacity: 1;
            
        }

        .main-btn{
            
            padding: 1em 4em;
         
            transition: background-color 500ms ease-in-out;

           
        
      

            &:hover{
                color: $background-color;
                background-color: $text-color;
            }

           
        }
       
    }

    .tool-icons{
    background-color: transparent;
    box-shadow: none;

   

    font-size: 2em;
        
    
    .icons-container{
        
        flex-wrap: wrap;
        animation: none;
      
        overflow: visible;
       
        width: 100%;
        
    
        .icon-img{
            background-color: #00303d;
     
            padding: 1em;
            width: 10%;

            
            
            &:hover{
                transform: scale(1.1);
            }
        }
    
        }
    }

    .about-me-article{

        flex-direction: row;
    
       font-size: 1.5em;
        position: relative;
       &::before{
        
        content: "About me";
        font-size: 10em;
        position: absolute;
        left: 0em;
        top: -.1em;
        color: $text-color;
        opacity: .1;
        
    }
    
    
      

     .article-content{
        
        .article-btn{
            
            transition: background-color 500ms ease-in-out;
            

        &:hover{
            
                color: $background-color;
            
                
                background-color: $text-color;
           
        }
        }
     }
    }
    
    .portfolio{

        .portfolio-title{
            font-size: 5em;
           text-align: start;
           color: $text-color;
           margin: .4em;
        }
    
        .portfolio-para{
            margin: 1em;
            font-size: 2em;
            text-align: start;
            color: $text-color;
        }
        
        .portfolio-container{
            justify-content: center;
            position: relative;
            
            padding: 2em 0 2em 0;

            &::after{
                content: "Projects";
                font-size: 15em;
                position: absolute;
                right: 0em;
                top: -1em;
                color: $text-color;
                opacity: .1;
            }
            .project{
        
                font-size: 1.1rem;
                text-align: center;
                flex: 0 0 19%;
              &:hover{
                border: 1px solid white;
                transform: scale(1.1);
                box-shadow: 0 0 20px black;
            }

             
               
              .desc-btn{
                margin: 1em;

                transition: background-color 500ms ease-in-out;
                

                &:hover{
                    a{
                        color: $background-color;
                    }
                        
                        background-color: $text-color;
                   
                }
            }
                
    
              
    
                
            }
        }
    }
    
}